.row{
    color: white;
    margin-left: 20px;
    margin-top: 20px;



}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;



}

.posters::-webkit-scrollbar{
    display: none;

}

.poster{
    max-height: 250px;
    margin-right: 10px;
    cursor: pointer;
}
.smallPoster{
    max-height: 150px;
    margin-right: 10px;
    cursor: pointer;



}
.poster:hover{
    transform: scale(1.1);
    transition:0.5s;
}
.smallPoster:hover{
    transform: scale(1.1);
    transition:0.5s;



}